@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: black;
  color: white;
  font-family: "Roboto", sans-serif;
  display: -webkit-flex;
  display: flex;
  /* align-items: center; */
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100vh;
  padding: 2rem;
}

h1 {
  margin: 2rem;
}

.board {
  border: 1rem solid #ff652f;
  background: #ff652f;
  width: 500px;
  height: 500px;
  display: grid;
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}

.squares {
  background: #272727;
  border: none;
  font-size: 5rem;
  /* font-weight: 800; */
  cursor: pointer;
  outline: none;
}

.X {
  color: red;
}
.O {
  color: green;
}

.info-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

h3 {
  padding: 1rem;
}

li {
  list-style: none;
}

.info-wrapper button {
  background: #272727;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0.5rem;
  margin: 0.5rem 1rem;
}
.SimpleTicTacToe {
  text-align: center;
  background-color: #f87060;
  height: 100vh;
}

table {
  margin: 0 auto;
}
td {
  width: 150px;
  height: 150px;
  border-right: 5px solid #ddd;
  border-bottom: 5px solid #ddd;
}

tr:last-child > td {
  border-bottom: none;
}
td:last-child {
  border-right:none;
}

td:hover {
  background-color: blue;
  cursor: pointer;
}
